.description {
    margin-top: 80px;
    &__wrap {
        display: flex;
        justify-content: space-between;
        gap: 30px;
    }
    .item {
        height: 100%;
        width: 49%;
    }
    &__block {
        width: 49%;
    }
    &__descr {
        margin-bottom: 0;
        font-size: 15px;
    }
    .item__bottom {
        justify-content: space-between;
        margin-top: 30px;
        padding: 0;
        .item__btn {
            margin: 0;
        }
    }
}

.info {
    margin-top: 80px;
    &__grid {
        display: grid;
        gap: 30px;
        grid-template-columns: 1fr 1fr;
    }
    &__item {
        display: flex;
        gap: 20px;
        flex-direction: column;
    }
    &__wrap {
        display: flex;
        gap: 20px;
    }
    &__img {
        max-height: 152px;
        display: flex;
        justify-content: center;
        width: 43%;
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__hotel {
        position: absolute;
        bottom: 0px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 12px;
        background: #FFF9F9;
        border-radius: 10px;
        width: 200px;
        padding: 10px;
        bottom: 10px;
        img {
            width: 12px;
        }
    }
    &__block {
        width: 43%;
    }
    &__name {
        margin: 0;
        padding: 0;
        font-size: 22px;
    }
    &__descr {
        font-weight: 400;
        font-size: 15px;
        line-height: 106.9%;
        color: #212121;
        opacity: 0.7;
        margin-top: 6px;
        margin-bottom: 0;
    }
}

.tour {
    margin-top: 80px;
    &__title {
        text-align: center;
        margin-bottom: 60px;
    }
    &__column {
        display: flex;
        flex-direction: column;
        gap: 15px;
    }
    &__line {
        margin-left: 85px;
        width: 2px;
    }
    &__item {
        margin-top: 30px;
        border-radius: 10px;
        position: relative;
        background: #FFF9F9;
    }
    &__day {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        color: #FFFFFF;
        position: absolute;
        top: -30px;
        left: 20px;
        height: 30px;
        width: 134px;
        background: #FF8000;
        border-radius: 78px 78px 0 0;
    }
    &__wrap {
        display: flex;
        flex-direction: column;
    }
    &__header {
        background: #BB2649;
        border-radius: 10px;
        display: flex;
        align-items: center;
        gap: 30px;
        padding: 17px 30px;
        color: #fff;
        .header__block {
            display: flex;
            flex-direction: column;
        }
        .header__time {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            padding: 3px 0;
            background: #CE637B;
            width: 55px;
            border-radius: 5px;
        }
        .header__name {
            font-weight: 700;
            font-size: 16px;
            max-width: 163px;
            color: #FFFFFF;
            padding: 0;
            margin: 0;
            padding-top: 5px;
        }
        .header__line {
            background: rgba(255, 255, 255, 0.5);
            border-radius: 4px;
            height: 2px;
            width: 30px;
        }
    }
    &__slider {
        width: 48%;
    }
    &__text {
        width: 48%;
        font-size: 15px;
        p {
            margin: 0;
            padding: 0;
            padding-bottom: 5px;
        }
        ul {
            padding-left: 25px;
            margin: 10px 0px;
        }
    }
    &__text.mono {
        width: 100%;
    }
    &__content {
        padding: 30px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 30px;
    }
    .swiper-slide {
        img {
            max-height: 250px;
            object-fit: cover;
            width: 100%;
        }
    }
    &__nav {
        margin-top: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__pagination {
        text-align: center;
        .swiper-pagination-bullet {
            background: #BB2649;
            opacity: 0.5;
            width: 9px;
            height: 9px;
        }
        .swiper-pagination-bullet-active {
            opacity: 1;
        }
    }
    &__prev, &__next {
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
        cursor: pointer;
    }
    &__prev:hover, &__next:hover {
        opacity: 0.75;
    }
} 

.present {
    margin-top: 80px;
    &__wrap {
        display: flex;
        justify-content: space-between;
    }
    &__title {
        max-width: 390px;
        font-size: 28px;
    }
    &__block {
        max-width: 112px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        img {
            width: 34px;
        }
    }
    &__name {
        text-align: center;
        font-size: 16px;
        font-weight: 700;
        padding: 0;
        margin: 0;
    }
    &__descr {
        text-align: center;
        font-size: 12px;
    }
}

.booking {
    margin-top: 60px;
    margin-bottom: 10px;
    &__wrap {
        position: relative;
        background: #BB2649;
        border-radius: 10px;
        color: #fff;
        padding: 13px 25px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__title {
        font-size: 22px;
        padding: 0;
        margin: 0;
        font-weight: 700;
    }
    .item__program {
        max-width: 100%;
        background: transparent;
        padding: 0;
        color: #fff;
        font-size: 14px;
    }
    .item__day {
        background: rgba(255, 255, 255, 0.06);
        color: #fff;
    }
    .item__price {
        color: #fff;
        font-size: 11px;
        span {
            font-size: 15px;
        }
    }
    &__block {
        padding: 0;
        align-items: center;
        gap: 30px;
    }
    .item__btn {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
    }
    .price__block {
        margin-right: 180px;
    }
}

@media (max-width: 1170px) {
    .description {
        &__wrap {
            flex-direction: column;
            gap: 20px;
        }
    }
}

@media (max-width: 992px) {
    .booking {
        &__wrap {
            padding: 10px 15px;
        }
        &__title {
            font-size: 18px;
        }
        &__block {
            gap: 15px;
        }
        .price__block {
            margin-right: 150px;
        }
    }
    .present {
        margin-top: 60px;
        &__title {
            font-size: 24px;
            max-width: 300px;
        }
    }
    .tour {
        margin-top: 60px;
        &__title {
            margin-bottom: 40px;
        }
        &__day {
            font-size: 14px;
            height: 25px;
            width: 112px;
            top: -25px;
        }
        &__column {
            gap: 10px;
        }
        &__line {
            margin-left: 75px;
        }
        &__item {
            margin-top: 25px;
        }
        &__header {
            padding: 15px;
            gap: 15px;
            .header__name {
                max-width: 140px;
                font-size: 14px;
            }
            .header__time {
                width: 50px;
                font-size: 11px;
            }
        }
        &__text {
            font-size: 14px;
        }
        &__nav {
            margin-top: 10px;
            .swiper-pagination-bullet {
                height: 7px;
                width: 7px;
            }
        }
        &__prev img, &__next img {
            width: 20px;
        }
    }
    .info {
        margin-top: 60px;
        &__grid {
            grid-template-columns: 1fr;
        }
        &__block {
            width: 49%;
        }
        &__item {
            gap: 15px;
        }
        &__name {
            font-size: 18px;
        }
        &__wrap {
            gap: 10px;
        }
        &__descr {
            font-size: 14px;
        }
        &__img {
            width: 49%;
            max-height: 192px;
        }
    }
    .description {
        margin-top: 60px;
        &__block {
            width: 100%;
        }
        .item {
            width: 100%;
        }
        .item__program {
            max-width: 200px;
            justify-content: center;
            font-size: 12px;
        }
    }
}

@media (max-width: 768px) {
    .booking {
        &__wrap {
            padding: 20px;
            flex-direction: column;
            align-items: center;
            gap: 15px;
        }
        &__block {
            flex-direction: column;
            align-items: center;
        }
        .price__block {
            margin: 0;
        }
        .item__btn {
            margin: 0;
            position: static;
        }
    }
    .present {
        &__title {
            text-align: center;
        }
        &__wrap {
            gap: 25px;
            align-items: center;;
            flex-direction: column;
        }
        &__block {
            gap: 5px;
        }
    }
    .tour {
        &__header {
            gap: 20px;
            .header__line {
                display: none;
            }
        }
        &__content {
            padding: 20px;
            flex-direction: column;
        }
        &__slider {
            width: 50%;
        }
        &__text {
            width: 100%;
        }
    }
}

@media (max-width: 568px) {
    .tour {
        &__header {
            display: grid;
            grid-template-columns: 1fr 1fr;
            .header__name {
                font-size: 12px;
            }
        }
        &__slider {
            width: 100%;
        }
        &__content {
            gap: 15px;
        }
        &__line {
            margin-left: 70px;
        }
    }
}

@media (max-width: 480px) {
    .present__title {
        font-size: 20px;
        max-width: 100%;
    }
    .description {
        .item__program {
            max-width: 160px;
            justify-content: center;
            font-size: 10px;
        }
        .item__btn {
            font-size: 12px;
            padding: 12px 10px;
        }
    }
    .info__hotel {
        width: 100%;
        gap: 10px;
        border-radius: 10px;
        padding: 8px;
        bottom: 0;
    }
}