@import url(https://fonts.googleapis.com/css?family=Nunito:200,300,regular,500,600,700,800,900,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic);

html {
  	box-sizing: border-box;
}

*,
*::before,
*::after {
  	box-sizing: inherit;
}

body {
  	color: #212121;
	font-family: 'Nunito', sans-serif;
}

a {
	color: #212121;
	text-decoration: none;
	opacity: 1;
	transition: opacity .2s ease-in-out;
}

a:hover {
  	opacity: 0.7;
}

.flex {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1140px;
}

.btn {
	background: #FF8000;
	border-radius: 10px;
	color: #fff;
	border: none;
	outline: none;
	padding: 15px 45px;
	font-size: 17px;
	opacity: 1;
	cursor: pointer;
	transition: opacity .2s ease-in-out;
}

.btn:hover {
	opacity: .85;
}

.title {
	font-size: 32px;
	padding: 0;
	margin: 0;
	font-weight: bold;
	span {
		color: #BB2649;
	}
}

.overflow {
	overflow: hidden;
}

.hero__dates {
	background: #FFFFFF;
	border-radius: 7px;
	padding: 10px;
	position: absolute;
	top: 65%;
	font-size: 14px;
	color: #000;
}

.hero-section {
	background: url('../img/hero-section-bg.jpg') center no-repeat;
	background-size: cover;
    background-position: center;
	border-radius: 25px;
	&__title {
		margin: 75px 0 !important;
	}
	&__wrap {
		position: relative;
		padding: 20px 0;
		display: flex;
		align-items: center;
		flex-direction: column;
	}
	&__breadcrumbs {
		display: flex;
		gap: 5px;
		align-items: center;
		a, span {
			color: #fff;
			font-size: 13px;
			opacity: 0.5;
		}
		a:hover {
			opacity: 0.4;
		}
	}
	&__line {
		position: absolute;
		width: 217px;
		height: 10px;
		bottom: -5px;
		background: #FF8000;
		border-radius: 10px;
	}
}

.search-section {
	&__wrap {
		padding: 45px 0px !important;
		.search {
			margin: 0;
		}
	}
	&__block {
		display: flex;
		align-items: center;
		gap: 20px;
		margin-top: 70px;
		margin-bottom: -20px;
		span {
			font-size: 14px;
			color: #ADADAD;
		}
	}
}

.section-search__offer {
	padding-bottom: 0 !important;
}

.news {
	margin-top: 60px;
	&__grid {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		gap: 30px;
	}
	&__showAll {
		font-weight: 400;
		font-size: 14px;
		color: #212121;
		padding: 20px 30px;
		background: #FFF9F9;
		border-radius: 5px;
		margin: 0 auto;
		display: flex;
		margin-top: 60px;
		text-align: center;
		max-width: 152px;
	}
}

.rekvizit {
	margin-top: 45px;
	display: flex;
	flex-direction: column;
	gap: 20px;
	&__block {
		display: flex;
		align-items: center;
		gap: 70px;
	}
	&__name {
		width: 130px;
		font-weight: 400;
		font-size: 15px;
		color: #ADADAD;
	}
	&__valuet {
		font-weight: 400;
		font-size: 15px;
		color: #212121;
	}
	&__article {
		margin-bottom: 50px !important;
	}
}
.contact {
	margin-top: 60px;
	&__wrap {
		gap: 10px;
		display: flex;
		align-items: center;
        justify-content: space-between;
	}
	&__block {
		display: flex;
		flex-direction: column;
		gap: 5px;
	}
	&__descr {
		font-size: 14px;
		font-weight: 500;
		color: #BB2649;
	}
	&__contact {
		font-weight: 700;
		font-size: 22px;
		color: #212121;
	}
	&__rekvizit {
		text-align: center;
		max-width: 230px;
		width: 100%;
		background: #FFFFFF;
		border: 1px solid #E7E7E7;
		box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
		border-radius: 10px;
		padding: 15px 40px;
		font-weight: 500;
		font-size: 15px;
		color: #212121;
	}
	&__form {
		text-align: center;
		max-width: 230px;
		width: 100%;
		padding: 15px 40px;
		font-weight: 700;
		font-size: 17px;
		color: #FFFFFF;
		background: #FF8000;
		border-radius: 10px;
	}
}

.dogovor {
	max-width: 100% !important;
}


@media (max-width: 992px) {
	.rekvizit__block {
		align-items: flex-start;
		flex-direction: column;
		gap: 7px;
	}
	.rekvizit__article {
		margin-bottom: 30px !important;
	}
	.rekvizit__name {
		width: 100%;
	}
	.contact {
		margin-top: 40px;
		&__block {
			gap: 3px;
			align-items: center;
		}
		&__contact {
			font-size: 20px;
		}
		&__form, &__rekvizit {
			font-size: 15px;
		}
	}
	.contact__wrap {
		align-items: center;
		gap: 20px;
		flex-direction: column;
	}
	.search-section {
		&__block {
			margin-top: 50px;
			span {
				font-size: 12px;
			}
		}
	}
	.news {
		&__grid {
			gap: 25px;
			grid-template-columns: 1fr 1fr 1fr;
		}
		&__showAll {
			margin-top: 40px;
		}
	}
	.hero__arrow {
		width: 32px;
	}
	.hero-section {
		&__line {
			width: 180px;
		}
		&__breadcrumbs {
			a, span {
				font-size: 12px;
			}
			gap: 4px;
		}
		&__title {
			margin: 60px 0px !important;
		}
	}
	.title {
		font-size: 28px;
	}
	.hero__dates {
		padding: 8px;
		font-size: 12px;
	}
}

@media (max-width: 768px) {
	.news {
		&__grid {
			gap: 20px;
			grid-template-columns: 1fr 1fr;
		}
		&__showAll {
			margin-top: 35px;
		}
	}
}

@media (max-width: 576px) {
	.search-section__block {
		align-items: flex-start;
		flex-direction: column;
		gap: 7px;
	}
	.title {
		font-size: 24px;
	}
}

@media (max-width: 480px) {
	.news {
		&__grid {
			grid-template-columns: 1fr;
		}
	}
}