.article {
    margin-bottom: 80px;
    &__title {
        max-width: 600px;
    }
    &__text {
        max-width: 850px;
        margin-top: 45px;
        p {
            margin: 0;
            padding: 0;
            margin-top: 20px;
            font-size: 15px;
            line-height: 144.9%;
            letter-spacing: 0.055em;
            color: #212121;
        }
    }
    &__mini-title {
        font-size: 22px;
    }
    &__photo {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        img {
            max-width: 350px;
            width: 100%;
            border-radius: 20px;
        }
    }
    &__quote {
        margin-top: 60px;
        display: flex;
        justify-content: flex-end;
        .quote {
            position: relative;
            max-width: 850px;
            padding: 40px;
            background: #FFF9F9;
            border-radius: 10px;
            p {
                margin: 0;
                padding: 0;
                font-size: 18px;
                line-height: 144.9%;
                letter-spacing: 0.055em;
                color: #212121;
            }
            &__block {
                margin-top: 30px;
                margin-left: auto;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                width: 100%;
                gap: 15px;
                img {
                    width: 42px;
                }
            }
            &__line {
                display: block;
                width: 130px;
                background: #BB2649;
                height: 2px;
            }
            &__name {
                font-size: 18px;
                font-weight: 500;
                letter-spacing: 0.055em;
                color: #212121;
            }
            &__descr {
                font-weight: 400;
                font-size: 12px;
                letter-spacing: 0.055em;
                color: #212121;
            }
            .quotes-1 {
                position: absolute;
                top: -20px;
                left: -10px;
            }
            .quotes-2 {
                position: absolute;
                bottom: -20px;
                right: 30px;
            }
        }
    }
    &__share {
        margin-top: 80px;
        padding: 30px 0;
        border-top: 1px solid #E8E8E8;
        border-bottom: 1px solid #E8E8E8;
        .container {
            width: 100%;
             display: flex;
             align-items: center;
            justify-content: space-between;
        }
    }
    &__view {
        padding: 0;
        margin: 0;
        font-size: 12px;
        span {
            color: #714040;
        }
    }
}

@media (max-width: 992px) {
    .article {
        &__photo {
            flex-direction: column;
            align-items: center;
            gap: 20px;
            img {
                max-width: 350px;
            }
        }
        &__text {
            p {
                font-size: 14px;
            }
        }
        .quote {
            p {
                font-size: 16px;
            }
        }
    }
}   