.footer {
    padding-top: 50px;
    padding-bottom: 20px;
    &__row {
        display: flex;
        justify-content: space-between;
    }
    &__block {
        span {
            font-weight: bold;
            font-size: 12px;
        }
    }
    &__title {
        color: #BB2649;
        padding: 0;
        margin: 0;
        font-size: 14px;
        padding-bottom: 7px;
    }
    &__link {
        margin: 0;
        padding: 0;
        color: #212121;
        font-weight: bold;
        font-size: 22px;
    }
    &__social {
        padding-top: 5px;
        display: flex;
        gap: 40px;
    }
    .line {
        margin: 20px 0;
        background: #E7E7EE;
        display: block;
        height: 1px;
        width: 100%;
    }
    .office__row {
        width: 100%;
        display: block;
    }
    &__office {
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding-top: 5px;
    }
    .office {
        &__name {
            padding: 0;
            margin: 0;
            font-size: 18px;
            font-weight: 700;
            padding-bottom: 8px;
        }
        &__block {
            display: flex;
            gap: 7px;
            align-items: center;
        }
        &__block:last-child {
            padding-top: 5px;
        }
        &__link {
            margin: 0;
            padding: 0;
            font-size: 12px;
        }
    }
    &__original {
        font-size: 12px;
    }
    &__document {
        font-size: 12px;
        gap: 35px;
        display: flex;
        align-items: center;
        color: rgba(33, 33, 33, 1);
    }
}

@media (max-width: 992px) {
    .footer {
        padding-top: 40px;
    }
    .footer__office {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px 60px;
    }
    .contact__row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px 60px;
    }
}

@media (max-width: 768px) {
    .footer__office {
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
    .footer__social {
        gap: 30px;
    }
    .contact__row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }
    .footer__document {
        flex-direction: column;
        gap: 5px;
        align-items: flex-end;
    }
    .footer__link {
        font-size: 18px;
    }
    .footer__logo {
        img {
            width: 92px;
        }
    }
    .footer__original {
        font-size: 11px;
        max-width: 100px;
    }
    .footer__document {
        font-size: 11px;
    }
}

@media (max-width: 480px) {
    .footer__office {
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
    .contact__row {
        grid-template-columns: 1fr;
        gap: 20px;
    }
}