.search {
    margin-top: 80px;
    padding: 40px;
    background: #fff;
    display: flex;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
    border-radius: 15px;
    width: 100%;
    justify-content: space-between;
     font-size: 14px;
    &__block span {
        display: flex;
    }
    input {
        box-sizing: border-box;
        max-width: 160px;
        padding: 17px;
        font-size: 14px;
        border: 1px solid rgba(231, 231, 231, 0.76);
        border-radius: 10px;
        outline: none;
        margin-top: 6px;
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
    }
    &__select {
        position: relative;
        img {
            cursor: pointer;
            position: absolute;
            right: 14px;
            top: 50%;
        }
    }
    .date__block {
        display: flex;
        max-width: 260px;
        .search__select input {
            font-size: 13px;
            max-width: 128px;
        }
        .search__select:first-child {
            position: relative;
            z-index: 2;
            margin-right: -10px;;
        }
    }
    .color__icon {
        position: absolute;
        left: 16px;
        top: 33%;
    }
    .icon__padding {
        text-align: center;
        padding-left: 40px;
    }
    .person__padding {
        padding-left: 50px;
        text-align: left;
    }
    &__btn {
        margin-top: auto;
        max-height: 52px;
    }
    .tooltip {
        position: relative;
        z-index: 5;
        border-radius: 10px;
        padding: 0;
        margin: 0;
        width: 260px;
        padding: 15px 0;
        font-size: 14px;
        background-color: #fff;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        display: flex;
        flex-direction: column;
        border: 1px solid #E7E7E7;
        li {
            padding: 8px 15px;
            border-bottom: 1px solid #E7E7EE;;
            list-style-type: none;
            cursor: pointer;
            color: inherit;
            transition: color .2s ease-in-out;
            .city {
                display: inline-block;
            }
            .country {
                display: inline-block;
                color: #ADADAD;
            }
        }
        li:hover {
            color: #BB2649;
        }

    }
    .hidden {
        display: none;
    }
    .pass {
        max-width: 150px;
        &__name {
            padding: 0;
            margin: 0;
        }
        &__block:first-child {
            border-bottom: 1px solid #E7E7EE;
            padding-bottom: 10px;
        }
        &__block:last-child {
            padding-top: 10px;
        }
        &__block {
            padding: 0 15px;
            span {
                font-size: 12px;
                color: #ADADAD;
            }
        }
        &__num {
            font-size: 14px !important;
            text-align: center !important;
            color: #282828 !important;
        }
        &__count {
            button {
                padding: 0;
                margin: 0;
                outline: none;
                border: none;
                cursor: pointer;
                background: transparent;
            }
            img {
                width: 32px;
                height: 32px;
                opacity: 1;
                transition: opacity .2s ease-in-out;
            }
            img:hover {
                opacity: 0.85;
            }
        }
    }
}

.air-datepicker-nav--title {
    color: #BB2649 !important;
}

.air-datepicker-nav--title i {
    color: #BB2649 !important;
}

.air-datepicker-nav--action path {
    stroke: #BB2649;
}

.air-datepicker-body--day-name {
    color: #ADADAD;
}

.air-datepicker-body--day-names {
    padding-bottom: 14px;
    border-bottom: 1px solid #E7E7EE;
}

.air-datepicker-body--cells.-days- {
    padding-top: 14px;
}

.air-datepicker-nav {
    border: none;
}

.air-datepicker-cell.-selected- {
    background: #FF8000;
    border-radius: 50%;
}

.air-datepicker-cell.-selected-:hover {
    background: #FF8000;
    border-radius: 50%;
}


.air-datepicker-cell.-current- {
    color: #31B766;
    background: none;
}

@media (max-width: 1170px) {
    .search__select input {
        max-width: 132px;
    }
    .search__btn {
        padding: 15px 35px;
        font-size: 16px;
    }
}

@media (max-width: 992px) {
    .search {
        margin-top: 60px;
        padding: 30px;
        flex-wrap: wrap;
        gap: 25px;
    }
    .search__select input {
        max-width: 124px;
    }
    .tooltip {
        max-width: 230px;
    }
}

@media (max-width: 768px) {
    .search {
        padding: 25px;
        flex-wrap: nowrap;
        flex-direction: column;
        gap: 15px;
    }
    .search__select input {
        max-width: 100%;
        width: 100%;
    }
    .search .date__block {
        max-width: 100%;
    }
    .search .date__block .search__select input {
        max-width: 100%;
    }
}