.jobs-offer {
    margin-top: 80px;
    &__title {
        font-weight: 800;
        font-size: 24px;
        color: #212121;
        margin: 0;
    }
    &__block {
        margin-top: 40px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 25px;
    }
    &__item {
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 12px;
        padding: 35px 0;
        img {
            width: 42px;
        }
    }
    &__name {
        max-width: 140px;
        font-size: 16px;
        text-align: center;
        font-weight: 700;
        color: #212121;
    }
    &__descr {
        font-size: 12px;
        text-align: center;
        color: #212121;
        max-width: 190px;
    }
}

.jobs {
    margin-top: 70px;
    margin-bottom: 20px;
    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title {
        text-align: center;
        color:#fff;
        padding: 18px 0;
        width: 540px;
        background: #BB2649;
        border-radius: 15px 15px 0 0;
    }
    &__block {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 15px;
        padding: 30px 25px;
        gap: 18px;
    }
    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 15px;
        padding: 5px 12px;
        .wrap {
            display: flex;
            flex-direction: column;
            gap: 2px;
        }
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 15px;
            padding: 15px;
            height: 100%;
            background: #FFF9F9;
            border-radius: 10px;
        }
    }
    &__country {
        font-weight: 400;
        font-size: 12px;
        color: #212121;
    }
    &__name {
        font-weight: 700;
        font-size: 16px;
        color: #212121;
    }
}

.job {
    margin-bottom: 5px;
}

.job__country {
    position: absolute;
    top: 70%;
    font-weight: 400;
    font-size: 12px;
    color: #FFFFFF;
}

@media (max-width: 992px) {
    .jobs {
        margin-top: 50px;
        &__title {
            padding: 15px 0;
            width: 480px;
        }
        &__block {
            padding: 20px;
        }
    }
}  


@media (max-width: 768px) {
    .jobs {
        margin-top: 50px;
        &__title {
            padding: 15px 0;
            width: 350px;
        }
        &__block {
            grid-template-columns: 1fr;
            padding: 15px;
        }
    }
    .jobs-offer {
        &__title {
            font-size: 20px;
        }
        margin-top: 60px;
        &__block {
            gap: 15px;
            grid-template-columns: 1fr 1fr;
        }
    }
}

@media (max-width: 480px) {
    .jobs {
        &__title {
            padding: 12px 0;
            width: 300px;
        }
        &__block {
            grid-template-columns: 1fr;
            padding: 10px;
            gap: 10px;
        }
        &__item {
            a {
                font-size: 14px;
                padding: 10px;
            }
        }
        &__name {
            font-size: 14px;
        }
    }
    .jobs-offer {
        margin-top: 50px;
        &__block {
            margin-top: 30px;
            grid-template-columns: 1fr;
        }
    }
}