.about {
    margin-top: 65px;
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }
    &__photo {
        width: 50%;
    }
    &__descr {
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
    &__title {
        line-height: 125%;
        font-size: 24px;
        margin: 0;
        padding: 0;
        color: #212121;
    }
    &__text {
        line-height: 125%;
        padding: 0;
        margin: 0;
        font-size: 15px;
        color: #212121;
    }
}

.license {
    margin-top: 100px;
    &__wrap {
        gap: 40px;
    }
    &__block {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__title {
        max-width: 340px;
    }
    &__btn {
        margin-top: 70px;
        max-width: 220px;
    }
    &__block.diplomas {
        flex-direction: row;
        gap: 30px;
        img {
            width: 100%;
        }
    }
    &__block.airways {
        gap:10px;
        align-items: center;;
        span {
            color: #212121;
            font-weight: 800;
            font-size: 15px;
        }
    }
}

.employee {
    margin-top: 100px;
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .title {
            max-width: 400px;
        }
    }
    .arrow__block {
        display: flex;
        align-items: center;
        gap: 30px;
        img {
            cursor: pointer;
            opacity: 1;
            transition: opacity .3s ease-in-out;
        }
        img:hover {
            opacity:.7;
        }
    }
    &__content {
        margin-top: 40px;
    }
    .swiper-slide {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 250px;
    }
    &__item {
        margin-top: auto;
        position: relative;
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 15px;
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__img {
        position: absolute;
        top: -60%;
        img {
            width: 118px;
        }
        span {
            position: absolute;
            top: 30px;
            left: 0;
            display: block;
            background: #FF8000;
            border-radius: 50%;
            width: 12px;
            height: 12px;
        }
        span:last-child {
            top: initial;
            width: 34px;
            height: 34px;
            left: inherit;
            bottom: 0;
            right: 0;
        }
    }
    &__title {
        margin: 0;
        padding: 0;
        margin-top: 15px;
        font-size: 18px;
        color: #212121;
    }
    &__descr {
        margin-top: 5px;
        font-size: 12px;
        color: #212121;
    }
    &__btn {
        margin-top: 10px;
        border-radius: 10px;
        background: #FFECD9;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #FF8000;
        font-size: 15px;
        font-weight: 600;
        width: 100%;
        height: 50px;
    }
}

.section-map {
    margin-top: 80px;
    margin-bottom: 20px;
    &__wrap {
        max-height: 620px;
        display: flex;
        justify-content: space-between;
        gap: 12px;
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 20px;
        padding: 30px;
    }
    &__block {
        max-width: 520px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 25px;
    }
    &__content {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__item {
        padding: 25px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid #E7E7E7;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.05);
        border-radius: 15px;
    }
    &__name {
        font-size: 18px;
        font-weight: 700;
        padding: 0;
        margin: 0;
    }
    .office__block {
        display: flex;
        gap: 5px;
    }
    .office__link {
        padding: 0;
        margin: 0;
        font-size: 14px;
    }
    &__line {
        width: 2px;
        display: flex;
        background: #E7E7E7;
        opacity: 0.5;
        border-radius: 10px;
    }
    #map {
        max-width: 540px;
        width: 100%;
        overflow: hidden;
        border-radius: 20px;
        max-height: 560px;
        height: 100%;
    }
}

@media (max-width: 992px) {
    .about {
        &__photo {
            width: 40%;
        }
        &__descr {
            width: 60%;
        }
        &__title {
            font-size: 20px;
        }
        &__text {
            font-size: 14px;
        }
    }
    .license {
        margin-top: 80px;
        &__btn {
            padding: 15px 35px;
            font-size: 16px;
            margin-top: 50px;
        }
        &__wrap {
            gap: 0px;
        }
        .diplomas {
            gap: 10px;
            img {
                max-width: 124px;
            }
        }
        .airways {
            img {
                max-width: 124px;
                width: 100%;
            }
        }
    }
    .employee {
        margin-top: 80px;
        .arrow__block {
            display: none;
        }
    }
    .section-map {
        margin-top: 70px;
        &__wrap {
            max-height: 100%;
            flex-direction: column;
            gap: 15px;
        }
        &__block {
            max-width: 100%;
        }
        &__content {
            gap: 10px;
        }
        #map {
            max-height: 450px;
            max-width: 100%;
            img {
                width: 100%;
                object-fit: cover;
            }
        }
        &__line {
            width: 100%;
            height: 2px;
        }
    }
}

@media (max-width: 768px) {
    .about {
        margin-top: 50px;
        &__wrap {
            flex-direction: column;

        }
        &__photo {
            width: 100%;
        }
        &__descr {
            width: 100%;
        }
    }
    .license {
        margin-top: 70px;
        &__wrap {
            flex-direction: column;
            gap: 20px;
        }
        &__title {
            max-width: 100%;
        }
        .diplomas {
            gap: 30px;
            img {
                max-width: 164px;
            }
        }
        .airways {
            span {
                font-size: 14px;
            }
            img {
                max-width: 164px;
                width: 100%;
            }
        }
    }
    .section-map {
        &__wrap {
            padding: 20px;
        }
        &__item {
            padding: 18px 10px;
        }
        &__name {
            font-size: 13px;
        }
        .office__link {
            font-size: 10px;
        }
    }
}

@media (max-width: 480px) {
    .about {
        margin-top: 40px;
    }
    .license {
        margin-top: 60px;
        &__btn {
            padding: 12px 30px;
            margin-top: 40px;
            font-size: 15px;
        }
        margin-top: 60px;
        .diplomas {
            gap: 20px;
            justify-content: space-between;
            img {
                max-width: 132px;
            }
        }
        .airways {
            img {
                max-width: 132px;
            }
        }
    }
    .employee {
        margin-top: 60px;
    }
    .section-map {
        margin-top: 60px;
        .office__block {
            gap: 2px;
        }
        &__wrap {
            padding: 15px;
        }
    }
}