.header {
    background-color: #fff;
    &__wrap {
        padding: 20px 0;
    }
    &__logo {
        img {
            max-width: 164px;
        }
    }
    &__nav {
        padding: 0;
        margin: 0;
        max-width: 460px;
        width: 100%;
    }
    &__link {
        font-size: 14px;
        list-style-type: none;
    }
    &__lang {
        font-size: 14px;
        justify-content: center;
        gap: 3px;
        .lang__item {
            color: #BCBCBC
        }
        .lang__item.active {
            color: #BB2649;
        }
    }
    &__contact {
        gap: 20px;
    }
    &__line {
        display: block;
        width: 1px;
        height: 38px;
        background-color: #E7E7E7;
    }
    .contact__block {
        display: flex;
        flex-direction: column;
        .phone {
            font-weight: bold;
            font-size: 18px;
        }
        .mail {
            font-size: 14px;
        }
    }
    .burger {
        display: none;
        position: relative;
        border: none;
        background: none;
        height: 20px;
        width: 30px;
        cursor: pointer;
    }

    .burger-line {
        width: 30px;
        height: 2px;
        background-color: #BB2649;
        transition: 0.3s all ease;
    }

    .top-line {
        position: absolute;
        top: 0;
        right: 0;
    }

    .mid-line {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
    }

    .bottom-line {
        position: absolute;
        left: 0;
        bottom: 0;
    }

    .burger.active > .top-line {
        transform: translateY(8px) rotate(45deg);
        transition: 0.3s all ease;
    }

    .burger.active > .mid-line {
        opacity: 0;
        transition: 0.3s all ease;
    }

    .burger.active > .bottom-line {
        transform: translateY(-10px) rotate(-45deg);
        transition: 0.3s all ease;
    }
}

.mobile-menu {
        display: none;
}

@media (max-width: 992px) {
    .header {
        &__logo img {
            max-width: 124px;
        }
        &__nav {
            max-width: 350px;
        }
        &__contact {
            gap: 10px;
        }
    }
}

@media (max-width: 768px) {
    .header {
        position: relative;
        z-index: 5;
        &__logo img {
            max-width: 132px;
        }
        &__nav, &__contact {
            display: none;
        }
        .burger {
            display: block;
        }
    }
    .mobile-menu {
        opacity: 1;
        visibility: visible;
        transition: all 0.2s ease-in-out;
        overflow-y: hidden;
        background:#BB2649;
        display: flex;
        position: absolute;
        top: 0;
        z-index: 3;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100vh;
        gap: 40px;
        .mobile-nav {
            display: flex;
            flex-direction: column;
            gap: 25px;
            .header__link a {
                color: #fff;
            }
        }
        .contact__block {
            display: flex;
            align-items: center;
            gap: 10px;
            flex-direction: column;
            a {
                color: #fff;
            }
        }
        .header__social {
            display: flex;
            gap: 15px;
        }
    }
    .mobile-menu.hidden {
        opacity: 0;
        visibility: hidden;
    }
}